.TaskItem, .TaskItemToday, .TaskItemDue, .TaskItemTomorrow, .TaskItemFuture{
  position: relative;
  background-color: transparent;
  z-index: 5;
}

.TaskItem::before, .TaskItemToday::before, .TaskItemDue::before, .TaskItemTomorrow::before, .TaskItemFuture::before{
  position: absolute;
  top: 0;
  content: "";
  z-index: -5;
  opacity: .5;
  border-radius: 5px;
  width: 100%;
  height: 100%;
}

.TaskItem::before {
  background-color: #35363a;
}

.TaskItemToday::before {
  background-color: darkgreen;
}

.TaskItemDue::before {
  background-color: rgb(100, 0, 0);
}

.TaskItemTomorrow::before {
  background-color: goldenrod;
}

.TaskItemFuture::before {
  background-color: #35363a;
  z-index: 6;
  opacity: .7;
}