.todoForm {
  background-color: #35363a;
  border: none;
  color: white;
}

.c-ph-light-subtle,
.c-ph-light-subtle::placeholder {
  color: white;
}

.c-border {
  border-top: 1px solid #3f474f;
  border-radius: 5px;
}

.c-date-input,
.c-date-input:active,
.c-date-input:focus {
  box-shadow: none;
  background: transparent;
  border-style: solid;
  border-width: 1px;
  border-color: #3f474f;
  color: white;
  margin: 0 20px;
}

.c-text-input,
.c-text-input:focus,
.c-text-input:active {
  color: white;
  box-shadow: none;
  border: 0;
  padding: 0 30px;
  background: transparent;
}